.embla {
  --slide-spacing: 1rem;
  --slide-size: 30%;
  --slide-height: 18rem;
  padding: 1.6rem;
}
.embla__viewport {
  overflow: hidden;
  border-radius: 40px;
}
.embla__container {
  backface-visibility: hidden;
  display: flex;
  margin-top: 5%;
  touch-action: pan-y;
  margin-left: calc(var(--slide-spacing) * -1);
}
.embla__slide {
  flex: 0 0 var(--slide-size);
  height: 300px;
  padding-left: var(--slide-spacing);
  position: relative;
}
.embla__slide__img {
  display: block;
  height: var(--slide-height);
  width: 100%;
  object-fit: cover;
}
.embla__slide__number {
  width: 4.6rem;
  height: 4.6rem;
  z-index: 1;
  position: absolute;
  top: 0.6rem;
  right: 0.6rem;
  border-radius: 50%;
  background-color: rgba(var(--background-site-rgb-value), 0.85);
  line-height: 4.6rem;
  font-weight: 900;
  text-align: center;
  pointer-events: none;
}
.embla__slide__number > span {
  color: var(--brand-primary);
  background-image: linear-gradient(45deg, var(--brand-primary), var(--brand-secondary));
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 1.6rem;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.embla__parallax {
  height: 100%;
  overflow: hidden;
}
.embla__parallax__layer {
  position: relative;
  height: 100%;
  width: 100%;
}
.embla__parallax__img {
  width: 250px;
  height: 300px;

  border-radius: 40px;
}

.small_screen_shadow {
  position: absolute;
  bottom: 65px;

  width: 100%;
  border-radius: 30px;
  height: 35.33%;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.9));
}

@media screen and (max-width: 461px) {
  .embla__parallax__layer {
    max-height: 320px;
  }
  .embla__parallax__img {
    width: 200px;
    height: 300px;

    border-radius: 40px;
  }
}
